#header_main {
  transition: all 280ms cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  z-index: 10;
}
#header_main .uk-navbar {
  background: #fff;
}
#header_main .uk-navbar-nav > li > a {
  color: #727272;
  font-size: 14px;
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  box-shadow: inset 0 -4px 0 rgba(255, 255, 255, 0);
  background: none !important;
  transition: all 280ms cubic-bezier(0.4, 0, 0.2, 1);
}
#header_main .uk-navbar-nav > li > a:hover,
#header_main .uk-navbar-nav > li > a:active {
  color: #727272;
}
#header_main .uk-navbar-nav > li.current_active a {
  box-shadow: inset 0 -4px 0 #7cb342;
}
#header_main.header-light {
  background: #fff;
}
#mobile_navigation_toggle {
  display: none;
}
#mobile_navigation .uk-offcanvas-bar {
  background: #fff;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.26);
  transition: transform 280ms cubic-bezier(0.4, 0, 0.2, 1);
}
#mobile_navigation .uk-offcanvas-bar:after {
  display: none;
}
#mobile_navigation .uk-offcanvas-bar ul {
  margin: 32px 0 0 0;
  padding: 0;
  list-style: none;
}
#mobile_navigation .uk-offcanvas-bar ul > li {
  padding: 0;
  margin: 0;
  list-style: none;
}
#mobile_navigation .uk-offcanvas-bar ul li a {
  display: block;
  font-size: 16px;
  font-weight: 500;
  line-height: 25px;
  padding: 8px 20px;
  color: #212121;
}
#mobile_navigation .uk-offcanvas-bar ul li a small {
  font-size: 12px;
  color: #727272;
  display: block;
  margin-top: -2px;
}
#mobile_navigation .uk-offcanvas-bar ul li a .menu_icon {
  width: 48px;
  text-align: left;
  display: inline-block;
  color: #727272;
  float: left;
}
#mobile_navigation .uk-offcanvas-bar ul li a .menu_icon .material-icons {
  font-size: 24px;
  vertical-align: top;
}
#mobile_navigation .uk-offcanvas-bar ul li a .menu_title {
  display: block;
  overflow: hidden;
}
.uk-offcanvas-page {
  transition: margin 280ms cubic-bezier(0.4, 0, 0.2, 1);
}
.header_sticky {
  padding-top: 64px;
}
@media only screen and (max-width: 959px) {
  .header_sticky {
    padding-top: 48px;
  }
}
.header_sticky #header_main {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
}
.header_shadow #header_main {
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.25);
}
.banner {
  height: 600px;
  color: #fff;
  position: relative;
}
.banner .uk-input-group {
  position: relative;
  display: table;
  border-collapse: separate;
}
.banner .uk-input-group .md-input {
  border: none;
}
.banner .uk-input-group .md-input::-webkit-input-placeholder {
  font-size: 16px;
}
.banner .uk-input-group .md-input::-moz-placeholder {
  font-size: 16px;
}
.banner .uk-input-group .md-input:-ms-input-placeholder {
  font-size: 16px;
}
.banner .uk-input-group .uk-input-group-addon {
  width: 1%;
  white-space: nowrap;
  vertical-align: middle;
  line-height: 1;
  text-align: center;
  padding: 4px 16px 0 0;
  font-size: 22px;
  min-width: 22px;
  display: table-cell;
}
.banner .md-card {
  margin: 250px auto;
  width: 50%;
  background: none;
  border-radius: 5px;
  zoom: 1.1;
}
.banner .md-card-toolbar {
  height: 48px;
  color: #fff;
  border: none;
  background-color: rgba(33, 150, 244, 0.73);
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  padding: 0 0 0 16px;
}
.banner .md-card-toolbar .md-card-toolbar-heading-text {
  color: #fff;
  font: italic 300 18px / 50px "Roboto", sans-serif;
}
.banner .md-card-toolbar .md-card-toolbar-actions {
  padding: 0;
}
.banner .md-card-toolbar .uk-tab {
  margin: 0;
  border: none;
}
.banner .md-card-toolbar .uk-tab li:nth-last-child(2) {
  border-top-right-radius: 5px;
}
.banner .md-card-toolbar .uk-tab li.uk-active {
  background-color: #004d8b;
  border: none;
}
.banner .md-card-toolbar .uk-tab li a {
  color: #fff;
  padding: 15px 12px !important;
  border: none;
}
.banner .md-card-content {
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  padding: 0 16px;
  background: #f2f2f2;
}
.banner .md-card-content .uk-switcher {
  width: 100%;
}
.uk-touch .slide_navigation {
  display: block !important;
}
.uk-slidenav {
  position: absolute;
  top: 50%;
  margin-top: -30px;
  color: #fff !important;
  background: rgba(0, 0, 0, 0.2);
  border-radius: 12px;
  font-size: 48px;
  transition: all 280ms cubic-bezier(0.4, 0, 0.2, 1);
}
.uk-slidenav-previous {
  left: 16px;
}
.uk-slidenav-previous:before {
  position: relative;
  left: -2px;
  top: -2px;
}
.uk-slidenav-next {
  right: 16px;
}
.uk-slidenav-next:before {
  position: relative;
  right: -2px;
  top: -2px;
}
.uk-slidenav:active,
.uk-slidenav:hover {
  background: rgba(0, 0, 0, 0.6);
  border-radius: 50%;
}
.uk-dotnav > li {
  border: 2px solid rgba(255, 255, 255, 0.8);
  padding: 2px;
  margin-right: 6px;
  border-radius: 50%;
}
.uk-dotnav > li.uk-active {
  border-color: #fff;
}
.uk-dotnav > li.uk-active > a {
  background: #7cb342;
}
.uk-dotnav > li > a {
  margin: 0;
  width: 14px;
  height: 14px;
}
.section {
  padding: 48px 0;
  position: relative;
}
.section_large {
  padding: 64px 0;
}
.section_dark {
  color: #fff;
}
.section_gallery {
  position: relative;
}
.section_gallery .slide_navigation {
  display: none;
}
.section_gallery:hover .slide_navigation {
  display: block;
}
[class*=uk-width].uk-container-center {
  float: none;
}
.login-modal .md-input {
  -webkit-box-shadow: inset 0 0 0px 9999px white !important;
}
@media only screen and (min-width: 1220px) {
  .login-modal {
    width: 400px;
  }
  .banner .uk-slideshow,
  .banner .uk-slideshow > li {
    height: auto;
    min-height: 360px;
  }
  .banner .uk-slideshow > li .slide_content_a,
  .banner .uk-slideshow > li .slide_content_b,
  .banner .uk-slideshow > li .slide_content_c {
    margin: 0;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.4);
    padding: 48px 48px 0;
    text-align: center;
  }
  .banner .uk-slideshow > li .slide_header {
    margin-bottom: 20px;
    color: #fff !important;
  }
  .banner .uk-slideshow > li p {
    font-size: 20px;
    color: #fff !important;
  }
  .banner .uk-slideshow > li p + * {
    margin-top: 24px;
  }
}
@media only screen and (max-width: 959px) {
  .login-modal {
    width: 400px;
  }
  #header_main {
    height: 48px;
  }
  #header_main .uk-navbar-brand {
    line-height: 48px;
    padding: 0;
  }
  #main_navigation {
    display: none;
  }
  #mobile_navigation_toggle {
    padding: 12px 4px 10px;
    display: inline-block;
  }
  #mobile_navigation_toggle i {
    font-size: 24px;
  }
}
@media only screen and (max-width: 767px) {
  .banner .uk-slideshow > li {
    background-size: auto 100%;
  }
  .banner .uk-slideshow > li .slide_content_a,
  .banner .uk-slideshow > li .slide_content_b,
  .banner .uk-slideshow > li .slide_content_c {
    margin: 0;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.4);
    padding: 24px 12px;
    text-align: center;
  }
  .banner .uk-slideshow > li .slide_header {
    font: 400 24px / 28px "Roboto", sans-serif;
    margin-bottom: 6px;
    color: #fff !important;
  }
  .banner .uk-slideshow > li p {
    font-size: 15px;
    color: #fff !important;
  }
  .banner .uk-slideshow > li p + * {
    margin-top: 24px;
  }
}
/* theme */
.uk-navbar {
  background: #03a9f4;
}
.uk-navbar-nav > li > a {
  color: #fff;
}
.uk-navbar-nav > li > a:hover,
.uk-navbar-nav > li > a:active {
  background: none;
  color: #fff;
}
.hero_section {
  background: #03a9f4;
}
